<template>
  <div>
    <b-modal
      id="modal_nuevo_taller"
      ref="modal"
      :title="tituloModal"
    >
      <form
        ref="form"
      >
        <b-form-group
          label-for="name-input"
          invalid-feedback="Name is required"
        >
          <label for="">Nombre del Taller</label>

          <b-form-input
            id="name-input"
            v-model="tallerNuevo.nombre"
            required
          />
          <label for="">Descripcin del Taller</label>

          <b-form-textarea
            id="textarea"
            v-model="tallerNuevo.descripcion"
            placeholder="Enter something..."
            rows="3"
            max-rows="6"
            required
          />
          <label for="">URL de la imagen</label>

          <input
            type="file"
            class="form-control"
            @change="getFile"
          >
        </b-form-group>

      </form>
      <template #modal-footer>
        <div class="w-100">
          <b-button
            variant="outline-success"
            class="float-right"
            @click="GuardarEditar"
          >
            {{ tituloBoton }} <feather-icon icon="SaveIcon" />
          </b-button>
        </div>
      </template>
    </b-modal>

  </div>
</template>

<script>
import {
  BButton, BFormGroup, BFormInput, BFormTextarea,
} from 'bootstrap-vue'
import { ref, computed } from '@vue/composition-api'
import store from '@/store'

export default {
  components: {
    BButton,
    BFormGroup,
    BFormInput,
    BFormTextarea,
  },
  setup() {
    const tituloModal = computed(() => store.state.deporte.tituloModal)
    const tituloBoton = computed(() => store.state.deporte.tituloBoton)
    const tallerNuevo = computed(() => store.state.deporte.tallerNuevo)
    const guardarOeditar = ref('')

    function getFile(e) {
      // eslint-disable-next-line prefer-destructuring
      this.tallerNuevo.img = e.target.files[0]
    }

    function GuardarEditar() {
      if (store.state.deporte.guardarEditar) { // true = guardar
        const formdata = new FormData()
        formdata.append('nombre', this.tallerNuevo.nombre)
        formdata.append('descripcion', this.tallerNuevo.descripcion)
        formdata.append('img', this.tallerNuevo.img)

        store.dispatch('deporte/addTaller', formdata).then(() => {
          this.$swal({
            position: 'top-end',
            icon: 'success',
            title: 'Taller registrado!!',
            showConfirmButton: false,
            timer: 1500,
            customClass: {
              confirmButton: 'btn btn-primary',
            },
            buttonsStyling: false,
          })
          store.dispatch('deporte/fetchTalleres')
          this.$bvModal.hide('modal_nuevo_taller')
        }).catch(error => {
          console.log(error)
        })
      } else { // false = actualizar
        const formdata = new FormData()
        formdata.append('id', this.tallerNuevo.id)
        formdata.append('nombre', this.tallerNuevo.nombre)
        formdata.append('descripcion', this.tallerNuevo.descripcion)
        formdata.append('img', this.tallerNuevo.img)
        formdata.append('_method', 'PUT')

        store.dispatch('deporte/updateTaller', formdata).then(() => {
          this.$swal({
            position: 'top-end',
            icon: 'success',
            title: 'Taller actualizado!!',
            showConfirmButton: false,
            timer: 1500,
            customClass: {
              confirmButton: 'btn btn-primary',
            },
            buttonsStyling: false,
          })
          store.dispatch('deporte/fetchTalleres')
          this.$bvModal.hide('modal_nuevo_taller')
        }).catch(error => {
          console.log(error)
        })
      }
    }

    return {
      tallerNuevo,
      getFile,
      tituloModal,
      guardarOeditar,
      GuardarEditar,
      tituloBoton,
    }
  },

}
</script>
