<template>
  <div>
    <taller-deportivo-nuevo />
    <b-card class="p-3">
      <b-button
        size="sm"
        variant="gradient-primary"
        class="mb-2"
        @click="abrirModal"
      >
        <feather-icon
          icon="PlusSquareIcon"
          class="mb-50"
        /> Nuevo taller
      </b-button>
      <br>
      <div>

        <b-row>
          <b-col
            v-for="(value, key, index) in talleres"
            :key="index"

            xl="4"
            lg="4"
            md="6"
            sm="12"
          >
            <b-card
              cols="12"
              :title="value.nombre"
              :img-src="value.img"
              img-alt="Image"
              img-top
              tag="article"
              style="max-width: 20rem;"
              class="mb-2 text-center border"
            >
              {{ value.descripcion }}

              <div class="d-flex flex-nowrap mt-1">
                <b-button
                  size="sm"
                  class="btn-block"
                  variant="outline-primary"
                  :to="{ name:'curso-taller-cultural', params: { id: value.id }}"
                >
                  <feather-icon
                    icon="EyeIcon"
                    size="16"
                  /> ver
                </b-button>

                <b-dropdown
                  variant="link"
                  no-caret
                  class="bordered"
                >
                  <template #button-content>
                    <feather-icon
                      icon="MoreVerticalIcon"
                      size="16"
                      class="align-middle text-body"
                    />
                  </template>
                  <b-dropdown-item @click="editarTaller(value)">
                    <feather-icon icon="EditIcon" />
                    <span class="align-middle ml-50">Editar</span>
                  </b-dropdown-item>
                  <b-dropdown-item
                    @click="eliminarTaller(value)"
                  >
                    <feather-icon icon="TrashIcon" />
                    <span class="align-middle ml-50">Eliminar</span>
                  </b-dropdown-item>
                </b-dropdown>
              </div>

            </b-card>

          </b-col>
        </b-row>

      </div>

    </b-card></div>
</template>

<script>
import {
  BRow, BCol, BCard, BButton, BDropdown, BDropdownItem,
} from 'bootstrap-vue'

import store from '@/store'
import { computed, onMounted, onUnmounted } from '@vue/composition-api'
import TallerDeportivoNuevo from './TallerDeportivoNuevo.vue'
import ServicioDeportivoStoreModule from './ServicioDeportivoStoreModule'

export default {
  components: {
    BCard,
    BButton,
    BRow,
    BCol,
    TallerDeportivoNuevo,
    BDropdown,
    BDropdownItem,
  },
  setup() {
    onUnmounted(() => {
      if (store.hasModule('deporte')) { store.unregisterModule('deporte') }
    })

    onMounted(() => {
      store.dispatch('deporte/fetchTalleres')
    })

    if (!store.hasModule('deporte')) store.registerModule('deporte', ServicioDeportivoStoreModule)

    const a = computed(() => store.state.deporte.numero)

    function abrirModal() {
      store.state.deporte.tituloModal = 'Nuevo Taller Cultural'
      store.state.deporte.tituloBoton = 'Guardar'
      store.state.deporte.guardarEditar = true
      store.state.deporte.tallerNuevo.nombre = ''
      store.state.deporte.tallerNuevo.descripcion = ''
      store.state.deporte.tallerNuevo.img = ''
      this.$bvModal.show('modal_nuevo_taller')
    }
    function editarTaller(value) {
      store.state.deporte.tituloModal = 'Editar Taller'
      store.state.deporte.tituloBoton = 'Actualizar'
      store.state.deporte.guardarEditar = false
      store.state.deporte.tallerNuevo.id = value.id
      store.state.deporte.tallerNuevo.nombre = value.nombre
      store.state.deporte.tallerNuevo.descripcion = value.descripcion
      store.state.deporte.tallerNuevo.img = ''
      this.$bvModal.show('modal_nuevo_taller')
    }
    function eliminarTaller(value) {
      this.$swal({
        title: `Esta seguro de eliminar el taller cultural ${value.nombre}`,
        text: 'Esta accin es irreversible!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, eliminar!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('deporte/deleteTaller', value.id).then(() => {
            this.$swal({
              icon: 'success',
              title: 'Realizado!',
              text: 'Taller cultural eliminado.',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
            store.dispatch('deporte/fetchTalleres')
          }).catch(error => {
            console.log(error)
          })
        }
      })
    }

    const saludar = () => {
      store.commit('deporte/saludar')
    }
    const talleres = computed(() => store.state.deporte.talleres)
    return {
      a,
      talleres,
      abrirModal,
      saludar,
      editarTaller,
      eliminarTaller,
    }
  },

}
</script>

<style scope>
article img {
  max-height: 150px;
}
</style>
