// import store from "@/store"
import axios from '@axios'

export default {
  namespaced: true,
  state: {
    talleres: [],
    tituloModal: '',
    tituloBoton: '',
    tallerNuevo: {
      id: '',
      nombre: '',
      descripcion: '',
      img: '',
    },
    guardarEditar: true,
  },
  getters: {},
  mutations: {
    listarTalleres(state, data) {
      state.talleres = data
    },
  },
  actions: {
    fetchTalleres(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-cultural/taller-global', { withCredentials: false })
          .then(response => { ctx.commit('listarTalleres', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    fetchTaller(ctx, { id }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/taller-global/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    addTaller(ctx, userData) {
      return new Promise((resolve, reject) => {
        axios
          .post('/servicio-cultural/taller-global', userData)
          .then(response => { console.log('exitosoPost', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    updateTaller(ctx, userData) {
      console.log('id', userData.get('id'))
      console.log('userdata', userData)
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-cultural/taller-global/${userData.get('id')}`, userData)
          .then(response => { console.log('exitoso', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteTaller(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`servicio-cultural/taller-global/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },

}
